import { compareSdkPCoreVersions } from './helpers/versionHelpers';

// import '../assets/css/sdkCssVars.scss';

interface Config {
  authConfig: {
    jwtUri: string;
    tokenUri: string;
  },
  serverConfig: {
    infinityRestServerUrl: string;
    sdkContentServerUrl: string;
  }
}

interface PegaInterface {
  initPega: (config: Config, element: Element) => Promise<void>;
  getNotificationCount: () => Promise<number>;
  showPage: (page: string, className: string) => Promise<void>;
  startCase: (caseTypeId: string, content: Object) => Promise<void>;
  continueCase: (assignmentId: string, className: string) => Promise<void>;
  initMashup: (el: Element) => Promise<void>
  initPortal: (el: Element) => Promise<void>
}

declare global {
  interface Window { 
    PConn: any;
    PCore: any;
    pega: PegaInterface;
    loadView: Function;
    loadPortal: Function;
    loadComponent: Function;
    loadMashup: Function;
    loadViewByName: Function;
    loadCase: Function;
    createCase: Function;
    loadAssignment: Function;
  }
}

window.pega = {
  initPega,
  initMashup,
  initPortal,
  getNotificationCount,
  showPage,
  startCase,
  continueCase
}

async function initMashup(element: Element): Promise<void> {
  window.PConn = await lazyLoad('mashup', element);
}
async function initPortal(element: Element): Promise<void> {
  window.PConn = await lazyLoad('portal', element);
}

async function showPage(pageName: string, className: string): Promise<void> {
  return window.PConn.getActionsApi().showPage(pageName, className);
}

async function startCase(caseTypeId: string, content: Object): Promise<void> {
  return window.PConn.getActionsApi().createWork(caseTypeId, { 
    containerName: 'primary',
    flowType: 'pyStartCase',
    caseInfo: { content: content }
  //   , pageName: 'pyEmbedAssignment' 
  });
  await window.PCore.getMashupApi().createCase(caseTypeId, 'root', {
    pageName: 'pyEmbedAssignment',
    startingFields: content
  });
}

async function continueCase(assignmentId: string, className: string): Promise<void> {
  return window.PConn.getActionsApi().openAssignment(assignmentId, className, {
    containerName: "primary",
    context: "root",
    isActionFromToDoList: true,
    isChild: undefined,
    target: ""
  });
}

async function lazyLoad(type, element) {
  return new Promise(resolve => {
    window.PCore.onPCoreReady(async renderObj => {
      window.PCore.getDebugger().toggleXRay(true);
      const React = (await import('react')).default;
      const initialRender = (await import('./initial_render')).default;
      const createPConnectComponent = (await import("./bridge/react_pconnect")).default;
  
      window.PCore.registerComponentCreator((c11nEnv, additionalProps = {}) => {
        const PConnectComp = createPConnectComponent();
        return (
            <PConnectComp {
              ...{
                ...c11nEnv,
                ...c11nEnv.getPConnect().getConfigProps(),
                ...c11nEnv.getPConnect().getActions(),
                additionalProps
              }}
            />
          );
      });

      console.log(`PCore ready!`);
      compareSdkPCoreVersions();
      initialRender(renderObj);
      
      resolve(renderObj.props.getPConnect());
    });

    const div = document.createElement('div');
    // div.attachShadow({mode: 'closed'});
    div.id = 'pega-root';
    // div.appendChild(createStyleSheet('/assets/css/sdkCssVars.css'));
    // div.appendChild(createStyleSheet('/assets/css/sdkStyles.css'));
    element.prepend(div);
    if(type === 'mashup') {
      window.loadMashup('pega-root', false);
    } else {
      window.loadPortal('pega-root', 'WebPortal');
    }
  });
}

function createStyleSheet(src) {
  const link = document.createElement('link');
  link.setAttribute('rel', 'stylesheet');
  link.setAttribute('type', 'text/css');
  link.setAttribute('href', src);
  return link;
}

async function getNotificationCount(): Promise<number> {
  const response = await window.PCore.getDataApiUtils().getData('D_pyGetCurrentUserNotifications');
  return response.data.resultCount;
}

